<template>
  <div>
    <v-container grid-list-md text-xs-center>
        <v-flex xs12 v-if="showChallengeList">
          <v-alert outline color="info" icon="info" :value="true">
            Choose {{this.chosenCategory.title.toLowerCase()}} challenge and solve using {{this.$route.path.substring(1)}}
          </v-alert>
        </v-flex>
      <v-layout row wrap v-if="showChallengeList">
        <v-flex xs4  md v-for="(item, i) in items" :key="i" >
          <v-list two-line>
              <v-list-tile
                @click="showDetails = !showDetails; chooseWhatToSendToPropAccordingToClickedName(item.challengeName)">
                <v-list-tile-avatar><v-icon>code</v-icon></v-list-tile-avatar>
                <v-list-tile-content @click="showChallengeList = !showChallengeList">
                  <v-list-tile-title v-html="item.challengeName"/>
                  <v-list-tile-sub-title v-html="item.challengeDifficulty"/>
                </v-list-tile-content>
              </v-list-tile>
          </v-list>
        </v-flex>
        <v-btn
          :key="icon"
          :color="color"
          dark
          large
          fab
          fixed
          bottom
          left
          @click = "goBackToChallengeSelection()"
        >
          <v-icon>{{ icon }}</v-icon>
        </v-btn>
      </v-layout>
    </v-container>
    <challenge v-if="showDetails" :items="itemDependingOnChallenge"/>
  </div>
</template>

<script>
  import {AXIOS} from '../networking/http-common'
  import Challenge from './Challenge.vue'
  import LanguageSelector from './LanguageSelector.vue'

  export default {
    name: "StringChallenge",
    props: ['chosenCategory'],
    components: {
      'challenge': Challenge,
      'language-selector': LanguageSelector
    },
    data() {
      return {
        lang: 'Java',
        showChallengeList: true,
        showDetails: false,
        items: [],
        icon: 'keyboard_arrow_left',
        color: 'cyan darken-3',
        itemDependingOnChallenge: [],
        categoryData: []
      }
    },
    methods: {
      goBackToChallengeSelection(){
        this.$router.go(0);
      },
      chooseWhatToSendToPropAccordingToClickedName(name) {
        this.itemDependingOnChallenge = this.items.find(function (item) {
          return item.challengeName = name
        });
      }
    },
    created() {
      this.categoryData = this.chosenCategory;
    },
    mounted: function () {
      AXIOS.get('/challenges' + this.$route.path + "/" + this.categoryData.title.toLowerCase())
        .then(response => {
          this.items = response.data
        })
        .catch(e => {
          this.errors.push(e)
        });
      this.$emit('clicked', this.response)
    },
  }
</script>


<style scoped>

</style>



