<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-flex xs12>
        <v-alert v-if="showCategories" outline color="info" icon="info" :value="true">
          Choose challenge category of {{this.$route.path.substring(1)}}
        </v-alert>
      </v-flex>
      <v-layout v-if="showCategories" row wrap>
        <v-flex xs6 v-for="(item, i) in this.categories" :key="i">
          <v-list two-line>
            <v-list-tile
              @click="showCategories = !showCategories; chooseWhatToSendToPropAccordingToClickedName(item.title)">
              <v-list-tile-content @click="show = !show">
                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-flex>
        <v-btn
          :key="icon"
          :color="color"
          dark
          large
          fab
          fixed
          bottom
          left
          @click="navigateToHome()"
        >
          <v-icon>{{ icon }}</v-icon>
        </v-btn>
      </v-layout>
    </v-container>
    <ChallengeSelector v-if="!showCategories" :chosenCategory="challengesDependingOnCategory"/>
  </div>
</template>
<script>
  import ChallengeSelector from './ChallengeSelector.vue'

  export default {
    name: "CategorySelector",
    components: {
      'ChallengeSelector': ChallengeSelector,
    },
    data() {
      return {
        show: true,
        showCategories: true,
        categories: [],
        icon: 'keyboard_arrow_left',
        color: 'cyan darken-3',
        challengesDependingOnCategory: [],
        javaCategories: [
          {
            title: "String",
            subtitle: "String manipulation with loops"
          },
          {
            title: "Array",
            subtitle: "Array manipulation with loops"
          },
          {
            title: "Algorithm",
            subtitle: "Usage of algorithms using different data structures"
          },
          {
            title: "Recursion",
            subtitle: "Problems with functions that call themselves"
          },
          {
            title: "Math",
            subtitle: "Math through coding"
          },
          {
            title: "Map",
            subtitle: "HashMap and Set"
          },
          {
            title: "Functional",
            subtitle: "Lambda expressions"
          },
          {
            title: "List",
            subtitle: "Problems with List usage"
          }
        ],
        pythonCategories: [
          {
            title: "String",
            subtitle: "String manipulation with loops"
          },
          {
            title: "Algorithm",
            subtitle: "Usage of algorithms using different data structures"
          },
          {
            title: "Recursion",
            subtitle: "Problems with functions that call themselves"
          },
          {
            title: "Math",
            subtitle: "Math through coding"
          },
          {
            title: "Dictionary",
            subtitle: "Dictionary problems"
          },
          {
            title: "List",
            subtitle: "Problems with List usage"
          }
        ]
      }
    },
    mounted: function () {
      if (this.$route.path.substring(1) === 'java') {
        this.categories = this.javaCategories;
      }
      else if (this.$route.path.substring(1) === 'python') {
        this.categories = this.pythonCategories;
      }
    },

    methods: {
      loadUriAccordingToLanguage(title) {
        console.log(this.$route.path.substring(1));
        let uri = "/" + title.toLowerCase();
        this.$router.push(uri);
      },
      chooseWhatToSendToPropAccordingToClickedName(name) {
        this.challengesDependingOnCategory = this.categories.find(function (category) {
          return category.title = name
        });
      },
      navigateToHome() {
        this.$router.push('/');
      },
    }
  }
</script>

<style scoped></style>



