<template>
  <v-flex xs3>
    <v-select
      :items="states"
      v-model="e1"
      prepend-icon="keyboard"
      hide-details
      placeholder="Java"
      overflow ></v-select>
  </v-flex>
</template>

<script>
  export default {
    data () {
      return {
        e1: "Java",
        states: [
          'Java', 'Python'
        ]
      }
    },
    watch: {
      e1: function () {
        this.$emit('langWasReset', this.e1)
      }
    }
  }
</script>

<style>

</style>



