<template>
  <v-container grid-list-md text-xs-center>
    <v-flex xs12>
      <v-alert outline color="info" icon="info" :value="true">
        Choose programming language you want to practice
      </v-alert>
    </v-flex>
    <v-layout row wrap>
      <v-flex xs6 v-for="(item, i) in items" :key="i">
        <v-list two-line>
          <v-list-tile @click="loadUriAccordingToLanguage(item.title)">
            <v-list-tile-content @click="show = !show">
              <v-list-tile-title v-html="item.title"/>
              <v-list-tile-sub-title v-html="item.subtitle"/>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
  export default {
    name: "LandingPage",
    data() {
      return {
        show: true,
        items: [
          {
              title: "Python",
              subtitle: "Challenges in Python language"
            },
            {
              title: "Java",
              subtitle: "Challenges in Java language"
            },
        ],
      }
    },
    methods: {
      loadUriAccordingToLanguage(title) {
        let uri = "/" + title.toLowerCase();
        this.$router.push(uri);
      }
    },
  }
</script>

<style scoped></style>



