<template>
  <div>
    <v-navigation-drawer
      fixed
      v-model="drawer"
      app
      temporary
    >
      <v-list dense>
        <v-list-tile @click="navigateToHome">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Home</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-group
          v-model="item.active"
          v-for="item in items"
          :key="item.title"
          :prepend-icon="item.action"
          no-action
        >
          <v-list-tile slot="activator">
            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile v-for="subItem in item.items" :key="subItem.title" @click="goToDocumentation(subItem.title)">
            <v-list-tile-content>
              <v-list-tile-title>{{ subItem.title }}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-icon>{{ subItem.action }}</v-icon>
            </v-list-tile-action>
          </v-list-tile>
        </v-list-group>

      </v-list>
    </v-navigation-drawer>

    <v-toolbar color="cyan darken-3" dark fixed app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title @click="navigateToHome" :style="{ cursor: 'pointer'}">TTÜ Coding challenges</v-toolbar-title>
    </v-toolbar>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        drawer: false,
        items: [
          {
            action: 'school',
            title: 'Documentation',
            active: true,
            items: [
              {title: 'Javadoc'},
              {title: 'Pythondoc'}
            ]
          }
        ]
      }
    },
    methods: {
      navigateToHome() {
        this.$router.push('/');
      },
      goToDocumentation(title) {
        let win;
        if (title === "Javadoc") {
          win = window.open('https://ained.ttu.ee/javadoc/index.html', '_blank');
          if (win) {
            win.focus();
          }
        } else if (title === "Pydoc") {
          win = window.open('https://ained.ttu.ee/pydoc/index.html', '_blank');
          if (win) {
            win.focus();
          }
        }
      },

    }

  }
</script>

<style></style>



