<template>
  <v-container grid-list-sm>
    <v-layout row wrap>
      <v-flex xs7>
        <v-jumbotron height="auto">
          <div class="title mb-3">{{challengeData.challengeName}}</div>
          <span class="subheading"> {{challengeData.challengeDescription}}</span>
        </v-jumbotron>
        <v-divider class="my-3"></v-divider>
        <codemirror class="CodeMirror" v-model="code"
                    :options="cmOptions"></codemirror>
        <v-btn
          large @click="sendCodeToTester()"
          :loading="loading"
          @click.native="loader = 'loading'"
          :disabled="loading"
          color="cyan darken-3"
          class="white--text"
        >Submit
        </v-btn>
        <v-btn
          :key="icon"
          :color="color"
          dark
          fab
          fixed
          bottom
          large
          left
          @click="goBackToChallengeSelection()"
        >
          <v-icon>{{ icon }}</v-icon>
        </v-btn>
      </v-flex>
      <v-flex xs1>

      </v-flex>
      <v-flex xs3>
        <pre class="testerResponse">{{testerResult}}</pre>
      </v-flex>
    </v-layout>
  </v-container>

</template>

<script>

  import {AXIOS} from '../networking/http-common'
  import 'codemirror/theme/cobalt.css'
  import 'codemirror/theme/neat.css'
  import 'codemirror/mode/clike/clike.js'
  import 'codemirror/addon/hint/show-hint.js'
  import 'codemirror/addon/edit/matchbrackets.js'
  import 'codemirror/addon/edit/closebrackets.js'

  export default {
    name: 'service',
    props: ['items'],
    data() {
      return {
        challengeData: [],
        code: '',
        icon: 'keyboard_arrow_left',
        color: 'cyan darken-3',
        testerResult: "",
        errors: [],
        loader: null,
        loading: false,
        cmOptions: {
          text: "hello",
          mode: "text/x-java",
          theme: 'neat',
          lineNumbers: true,
          line: true,
          matchBrackets: true,
          autoCloseBrackets: true,
          lineWrapping: true
        }
      }
    },
    methods: {
      goBackToChallengeSelection() {
        this.$router.go(0);
      },
      sendCodeToTester() {
        AXIOS.post('http://193.40.252.119:9090/test/sync', {
          source: {
            files: [
              {
                path: "Challenge.java",
                content: 'class Challenge{ ' + this.code + '}'
              }
            ]
          },
          project: {
            name: this.challengeData.challengeName,
            course: "154858iapb_test"
          }
        }).then(response => {
          this.testerResult = this.parseTesterResult(response.data);
        }).catch(function (error) {
          console.log(error)
        });
      },
      parseTesterResult(response) {
        return response.results[0].output;
      }
    },
    mounted() {
      this.challengeData = this.items;
      this.code = this.challengeData.challengeTemplate;
    },
    computed: {
      codemirror() {
        return this.$refs.myCm.codemirror
      }
    },
    watch: {
      loader() {
        const l = this.loader;
        this[l] = !this[l];

        setTimeout(() => (this[l] = false), 6000);

        this.loader = null
      }
    }
  }

</script>

<style scoped>
  /*.CodeMirror {*/
    /*font-family: Arial, monospace;*/
    /*font-size: 14px;*/
    /*border: 1px solid #eee*/
  /*}*/

  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }

  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .testerResponse{
    font-family: Roboto,serif;
  }

</style>



